import axios from "axios";
import React from "react";
import toast from "react-hot-toast";
import { useNavigate } from "react-router-dom";

const ForgetPassword = () => {
  let navigate = useNavigate();

  let userEmail = "";
  let userCode = "";

  async function getUserEmail() {
    try {
      let { data } = await axios.post(
        "https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords",
        {
          email: userEmail,
        }
      );

      if (data.statusMsg === "success") {
        toast.success(data.message, {
          duration: 2000,
          position: "top-right",
        });
      } else {
        toast.error("Error", {
          duration: 2000,
          position: "top-right",
        });
      }
    } catch (error) {
      console.log(error);
    }
  }

  async function getUserCode() {
    try {
      let { data } = await axios.post(
        "https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode",
        {
          resetCode: userCode,
        }
      );

      if (data.status === "Success") {
        toast.success("verified successfuly", {
          duration: 2000,
          position: "top-right",
        }
        );
        setTimeout(() => {
         navigate("/resetpassword")
        }, 500);
      } else {
        toast.error("Error", {
          duration: 2000,
          position: "top-right",
        });
      }
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <>
      <div className="w-50 m-auto py-5">
        <label htmlFor="forgetPass">please enter your eamil</label>
        <input
          type="email"
          id="forgetPass"
          className="form-control mb-4"
          placeholder="email"
          onChange={function (e) {
            userEmail = e.target.value;
          }}
        />

        <label htmlFor="code">Enter your verification code</label>
        <input
          type="number"
          id="code"
          className="form-control mb-5"
          placeholder="code"
          onChange={function (e) {
            userCode = e.target.value;
          }}
        />

        <button onClick={getUserEmail} className="btn btn-primary">
          send code
        </button>

        <button onClick={getUserCode} className="btn btn-success mx-3">verify</button>
      </div>
    </>
  );
};

export default ForgetPassword;
